@charset "UTF-8";
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: sans-serif;
  font-size: 0.9rem;
  color: #222222;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

.logo {
  width: 120px;
  line-height: 1px;
  padding: 10px 0;
}
.logo a {
  display: block;
}

.wrapper {
  max-width: 960px;
  margin: 0 auto 100px auto;
  padding: 0 4%;
  text-align: center;
}

.section-title {
  display: inline-block;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 60px;
  border-bottom: 1px solid #222222;
}

.content-title {
  font-size: 1rem;
  margin: 10px 0;
}

/*-------------------------------------------
ヘッダー
-------------------------------------------*/
#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}
#header ul {
  display: flex;
  padding: 10px 0;
}
#header ul li {
  margin-left: 30px;
}
#header ul li a {
  color: #222222;
}
#header ul li a:hover {
  opacity: 0.8;
}

/*-------------------------------------------
Mainvisual
-------------------------------------------*/
#mainvisual {
  margin-bottom: 80px;
}
#mainvisual img {
  width: 100%;
  max-width: 1920px;
  height: 600px;
  object-fit: cover;
}
@media (max-width: 600px) {
  #mainvisual img {
    height: calc(100vh - 60px);
  }
}

/*-------------------------------------------
About
-------------------------------------------*/
#about .content {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 600px) {
  #about .content {
    flex-direction: column;
  }
}
#about img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 30px;
}
@media (max-width: 600px) {
  #about img {
    margin-right: 0;
  }
}
#about .text {
  text-align: left;
}

/*-------------------------------------------
Bicycle
-------------------------------------------*/
#bicycle ul {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 600px) {
  #bicycle ul {
    flex-direction: column;
  }
}
#bicycle ul li {
  width: 32%;
}
@media (max-width: 600px) {
  #bicycle ul li {
    width: 100%;
    margin-bottom: 30px;
  }
}

/*-------------------------------------------
footer
-------------------------------------------*/
#footer {
  font-size: 0.5rem;
  padding: 10px 0;
  text-align: center;
}